// main: ../style.scss
@import '../global/vars';
@import '../global/mixin';
@charset "UTF-8";
@font-face {
  font-family: "paperplane-blank-theme-social-icons";
  src: url("assets/fonts/paperplane-blank-theme-social-icons.eot");
  src: url("assets/fonts/paperplane-blank-theme-social-icons.eot?#iefix") format("embedded-opentype"), url("assets/fonts/paperplane-blank-theme-social-icons.woff") format("woff"), url("assets/fonts/paperplane-blank-theme-social-icons.ttf") format("truetype"), url("assets/fonts/paperplane-blank-theme-social-icons.svg#paperplane-blank-theme-social-icons") format("svg");
  font-weight: normal;
  font-style: normal;
}

[data-icon]:before {
  font-family: "paperplane-blank-theme-social-icons" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="social-icorn"]:before,
[class*=" social-icorn"]:before {
  font-family: "paperplane-blank-theme-social-icons" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icorn-newsletter-social:before {
  content: "\61";
}

.icorn-facebook-social:before {
  content: "\62";
}

.icorn-facebook-messenger-social:before {
  content: "\63";
}

.icorn-instagram-social:before {
  content: "\64";
}

.icorn-linkedin-social:before {
  content: "\65";
}

.icorn-telegram-social:before {
  content: "\66";
}

.icorn-tiktok-social:before {
  content: "\67";
}

.icorn-twitter-social:before {
  content: "\68";
}

.icorn-vk-social:before {
  content: "\69";
}

.icorn-whatsapp-social:before {
  content: "\6a";
}

.icorn-oblique:before {
  content: "\6b";
}

.icorn-spotify-social:before {
  content: "\6c";
}

.icorn-youtube-social:before {
  content: "\6d";
}